<template>
  <div class="receiveCard-box">
    <div class="receiveCard">
      <div>{{adItem.rowCol.title}}</div>
      <div>
        <div v-for="(item, i) in test" :key="i">
          <span>{{item.title}}</span>
          <input v-model.number="item.value" type="text" :max="item.max" @input="change(item.value, i)" @blur="testValue(item.value, i)" />
          <span class="spans">{{item.param}}</span>
        </div>
      </div>
    </div>
    <div class="receiveCard">
      <div>{{adItem.xAndy.title}}</div>
      <div>
        <div v-for="(item, i) in tests" :key="i">
          <span>{{item.title}}</span>
          <input v-model.number="item.value" type="text" :max="item.max" @input="changes(item.value, i)" @blur="testValues(item.value, i)">
          <span class="spans">{{item.num}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  // import $ from 'zepto';
  import { Toast } from 'mint-ui';
  import FastClick from 'fastclick';
  export default {
    data() {
      return {
      };
    },
    props: ['adItem', 'resolution', 'boxParam'],
    computed: {
			/*网口行列：行数*列数<512  起始位置：0~4096
			网口限制：行数*箱体高度+起始位置Y < 4096   列数*箱体宽度+起始位置X < 2160
			网口带宽限制1：行数*列数*箱体宽度*箱体高度*60<39583333
			网口带宽限制2：（行数*箱体高度+起始位置Y +160）*（列数*箱体宽度+起始位置X +62）* 60<(39583333*6)
      */
      test() {
        // （行数*箱体高度+起始位置Y +160）*（列数*箱体宽度+起始位置X +62）* 60<(39583333*6)
        let list = this.adItem.rowCol.list;
        list[0].param = list[0].value * Number(this.boxParam.width) + 'px';
        list[1].param = list[1].value * Number(this.boxParam.height) + 'px';
        return list;
      },
      tests() {
        let list = this.adItem.xAndy.list;
        return list;
      }
    },
    mounted() {
      document.removeEventListener('fastclick', () => { }, false);
    },
    methods: {
      getCol: function(row = 1, x = 0, y = 0) {
        x = +x;
        y = +y;
        var col, W = Number(this.boxParam.width), H = Number(this.boxParam.height);
        col = 512 / row;
        col = (col * W + x < 4096) ? col : (4096 - x) / W;
        col = row * col * W * H * Number(this.resolution.rate) < 39583333 ? col : 39583333 / W / H / Number(this.resolution.rate) / row;
        col = (row * H + y + 62) * (col * W + x + 160) * Number(this.resolution.rate) < 237499998 ? col : (237499998 / Number(this.resolution.rate) / (row * H + y + 62) - 160 - x) / W;
        return Math.floor(col);
      },
      getRow: function(col = 1, x = 0, y = 0) {
        // 网口限制：行数*箱体高度+起始位置Y < 4096   列数*箱体宽度+起始位置X < 2160
        // 网口带宽限制1：行数*列数*箱体宽度*箱体高度*60<39583333
        // 网口带宽限制2：（行数*箱体高度+起始位置Y +160）*（列数*箱体宽度+起始位置X +62）* 60<(39583333*6)
        x = +x;
        y = +y;
        var row, W = Number(this.boxParam.width), H = Number(this.boxParam.height);
        row = 512 / col;
        //row * H + y < 4096;
        row = (row * H + y < 2160) ? row : (2160 - y) / H;
        row = row * col * W * H * Number(this.resolution.rate) < 39583333 ? row : 39583333 / W / H / Number(this.resolution.rate) / col;
        row = (row * H + y + 62) * (col * W + x + 160) * Number(this.resolution.rate) < 237499998 ? row : (237499998 / Number(this.resolution.rate) / (col * W + x + 160) - 62 - y) / H;
        return Math.floor(row);
      },
      getX: function(row = 1, col = 1, y = 0) {
        y = +y;
        var x, W = Number(this.boxParam.width), H = Number(this.boxParam.height);
        x = x < 0 ? 0 : x;
        x = x > 4096 ? 4096 : x;
        x = 4096 - (col * W);
        x = (row * H + y + 62) * (col * W + x + 160) * Number(this.resolution.rate) < 237499998 ? x : 237499998 / Number(this.resolution.rate) / (row * H + y + 62) - 160 - (col * W);
        return Math.floor(x);
      },
      getY: function(row = 1, col = 1, x = 0) {
        x = +x;
        var y, W = Number(this.boxParam.width), H = Number(this.boxParam.height);
        y = y < 0 ? 0 : y;
        y = y > 4096 ? 4096 : y;
        y = 2160 - (row * H);
        y = (row * H + y + 62) * (col * W + x + 160) * Number(this.resolution.rate) < 237499998 ? y : 237499998 / Number(this.resolution.rate) / (col * W + x + 160) - 62 - (row * H);
        return Math.floor(y);
      },
      change(val, i) {
        var max, self = this;
        if(i === 0) {
          max = this.getRow(this.test[1].value, this.tests[0].value, this.tests[1].value);
          this.test[0].max = max;
        } else {
          max = this.getCol(this.test[0].value, this.tests[0].value, this.tests[1].value);
          this.test[1].max = max;
        }
        if(val > max) {
          setTimeout(function() {
            self.test[i].value = max;
          }, 1000);
        }
        let reg = /^[0-9]+$/;
        for(let j = 0; j < val.length; j++) {
          if(!reg.test(val[j])) {
            self.$set(self.test[i], 'value', 1);
            Toast({
              message: self.$t('common.number'),
              duration: 1000
            });
          }
        }
      },
      changes(val, i) {
        var max, self = this;
        if(i === 0) {
          max = this.getX(this.test[0].value, this.test[1].value, this.tests[1].value);
          this.tests[0].max = max;
        } else {
          max = this.getY(this.test[0].value, this.test[1].value, this.tests[0].value);
          this.tests[1].max = max;
        }
        if(val > max) {
          setTimeout(function() {
            self.tests[i].value = max;
          }, 1000);
        }
        let reg = /^[0-9]+$/;
        for(let q = 0; q < val.length; q++) {
          if(!reg.test(val[q])) {
            self.$set(self.tests[i], 'value', 1);
            Toast({
              message: self.$t('common.number'),
              duration: 1000
            });
          }
        }
      },
      testValue(val, i) {
        let reg = /^[0-9]+$/;
        if(!reg.test(val)) {
          this.$set(this.test[i], 'value', 1);
          Toast({
            message: this.$t('common.number'),
            duration: 1000
          });
        }
      },
      testValues(val, i) {
        let reg = /^[0-9]+$/;
        if(!reg.test(val)) {
          this.$set(this.tests[i], 'value', 1);
          Toast({
            message: this.$t('common.number'),
            duration: 1000
          });
        }
      }
    },
    destroyed() {
      if('addEventListener' in document) {
        document.addEventListener(
          'fastclick',
          function() {
            FastClick.attach(document.body);
          },
          false
        );
      }
    }
  };
</script>
<style lang="less" scoped>
  .receiveCard {
    padding: 18px 35px;
    overflow: hidden;
    border-bottom: 1px solid #4f5054;
    display: flex;
    justify-content: space-between;
    > div {
      float: left;
    }
    > div:nth-child(1) {
      width: 25%;
      text-align: left;
      margin-top: 26px;
    }
    > div:nth-child(2) {
      // margin-left: 72px;
      width: 70%;
      overflow: hidden;
      padding-right: 12px;
      > div:nth-child(1) {
        margin-top: 12px;
      }
      > div {
        display: flex;
        justify-content: space-between;
        float: left;
        margin-top: 30px;
        > span:nth-child(1) {
          display: flex;
          width: 53px;
          text-align: left;
          align-items: center;
        }
        .spans {
          width: 106px;
          display: flex;
          text-align: left;
          align-items: center;
          font-size: 30px;
          color: #aeaeae;
        }
      }
      input {
        width: 199px;
        height: 67px;
        text-indent: 15px;
        box-sizing: border-box;
        outline: none;
        border: 1px solid #aeaeae; /*no*/
        border-radius: 6px;
        margin-left: 58px;
        margin-right: 28px;
        background: none;
        color: #ddd;
        font-size: 16px; /*no*/
      }
    }
  }
  @media screen and (orientation: landscape) {
    .receiveCard {
      padding: 18px 35px;
      overflow: hidden;
      border-bottom: 1px solid #4f5054;
      display: flex;
      justify-content: space-between;
      > div {
        float: left;
      }
      > div:nth-child(1) {
        width: 20%;
        text-align: left;
        margin-top: 12px;
        display: flex;
        align-items: center;
      }
      > div:nth-child(2) {
        // margin-left: 72px;
        width: 80%;
        overflow: hidden;
        > div {
          display: flex;
          justify-content: space-between;
          float: left;
          margin-top: 10px;
          margin-right: 70px;
          > span:nth-child(1) {
            display: flex;
            width: 53px;
            text-align: left;
            align-items: center;
          }
          .spans {
            width: 106px;
            display: flex;
            text-align: left;
            align-items: center;
          }
        }
        input {
          width: 199px;
          height: 65px;
          padding: 24px 23px;
          box-sizing: border-box;
          outline: none;
          border: 1px solid #fff; /*no*/
          border-radius: 6px;
          margin-left: 58px;
          margin-right: 20px;
          background: none;
          color: #ddd;
        }
      }
    }
  }
</style>


