<template>
  <div class="selectScreenMode advanced_all">
    <vx-header :title="$t('selectScreenMode.advancedScreen')" :showback="1" :showsave="1" @save="advSave"></vx-header>
    <div class="vx-container" @scroll="scroll">
      <div class="advancedScreen">

        <div class="advancedScreen-content">
          <div class="advancedScreen-list">
            <div class="screen_name">
              <div>{{$t('selectScreenMode.deviceName')}}</div>
              <div>{{deviceName}}</div>
            </div>
            <div class="screen_address">
              <div>{{$t('selectScreenMode.deviceAddress')}}</div>
              <div>{{ip}}</div>
            </div>
          </div>
          <div v-for="(adItem, index) in advancedScreen" :key="index">

            <div class="advancedScreen-title">{{adItem.title}}</div>

            <div class="advancedScreen-list">

              <rcxy :ad-item="adItem" :resolution="resolution" :boxParam="boxParam"></rcxy>

              <div class="routeOrder">

                <div>{{adItem.routeOrder.title}}</div>

                <div>
                  <div class="imgParents" v-for="(item, index) in adItem.routeOrder.list" :key="index">
                    <div :class="item.checked?item.active:item.norClass" @click='selectRouteOrder(index, item, adItem.routeOrder.list)'></div>
                  </div>
                </div>

              </div>

            </div>

          </div>

        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import rcxy from './rcxy';
  import { setLoc, getLoc, setSen, getSen, clearLocalKey } from '../../utils';
  import { Indicator } from 'mint-ui';
  import htp from "../../api/http";
  import url from '../../api/apiurl';
  import { mapMutations, mapGetters } from 'vuex';
  export default {
    created() {
      this.getIpnNme();
      this.pageLocData();
    },
    components: {
      rcxy
    },
    mounted() {
    },
    data() {
      return {
        ip: '',
        deviceName: '',
        advancedScreen: [],
        rowCols: [{
          title: this.$t('selectScreenMode.X'),
          value: 1,
          num: 'PX'
        }, {
          title: this.$t('selectScreenMode.Y'),
          value: 1,
          num: 'PX'
        }],
        port: [],
        resolution: '',
        boxParam: ''
      };
    },
    methods: {
      ...mapMutations(['setSelectPort']),
      //获取高级点屏数据
      pageLocData() {
        this.port = getLoc('port');
        this.resolution = getLoc('resolution');
        this.boxParam = getLoc('boxParam');
        this.formatAdvancedScreen();
      },
      //获取设备信息数据
      getIpnNme() {
        htp.get(url.getDeviceInfo).then(res => {
          if(res.status === 10000000) {
            this.ip = res.data.deviceIp;
            this.deviceName = res.data.deviceName;
          }
        });
      },
      //配置高级点屏数据
      formatAdvancedScreen() {
        let that = this;
        that.port.forEach(function(item) {
          var obj = {};
          obj.title = that.$t('selectScreenMode.networkPort') + item.portId;
          obj.id = item.portId;
          obj.rowCol = {
            title: that.$t('selectScreenMode.receivingCardQuantity'),
            list: [{
              title: that.$t('selectScreenMode.row'),
              value: item.portY
            }, {
              title: that.$t('selectScreenMode.column'),
              value: item.portX
            }]
          };
          obj.xAndy = {
            title: that.$t('selectScreenMode.offsetCoordinates'),
            list: [{
              title: that.$t('selectScreenMode.X'),
              value: item.portOffsetX,
              num: 'px'
            }, {
              title: that.$t('selectScreenMode.Y'),
              value: item.portOffsetY,
              num: 'px'
            }]
          };
          obj.routeOrder = {
            title: that.$t('selectScreenMode.routeOrder'),
            list: [{
              norClass: 'normal_img1',
              checked: false,
              active: 'select_img1',
              walkType: 0
            }, {
              norClass: 'normal_img2',
              checked: false,
              active: 'select_img2',
              walkType: 1
            }, {
              norClass: 'normal_img3',
              checked: false,
              active: 'select_img3',
              walkType: 2
            }, {
              norClass: 'normal_img4',
              checked: false,
              active: 'select_img4',
              walkType: 3
            }, {
              norClass: 'normal_img5',
              checked: false,
              active: 'select_img5',
              walkType: 4
            }, {
              norClass: 'normal_img6',
              checked: false,
              active: 'select_img6',
              walkType: 5
            }, {
              norClass: 'normal_img7',
              checked: false,
              active: 'select_img7',
              walkType: 6
            }, {
              norClass: 'normal_img8',
              checked: false,
              active: 'select_img8',
              walkType: 7
            }]
          };
          obj.routeOrder.list.forEach((v, i) => {
            if(item.walkType === v.walkType) {
              v.checked = true;
            }
          });
          that.advancedScreen.push(obj);
        });
      },
      //高级点屏下发事件
      advSave() {
        let params = this.advancedScreen;
        let portInfoList = [];
        let savedata = {};
        params.forEach(function(v, i) {
          let opt = {};
          opt.portId = v.id;
          opt.portX = Number(v.rowCol.list[1].value);
          opt.portY = Number(v.rowCol.list[0].value);
          opt.portOffsetX = Number(v.xAndy.list[0].value);
          opt.portOffsetY = Number(v.xAndy.list[1].value);
          var index;
          v.routeOrder.list.forEach(function(item, i) {
            if(item.checked) {
              index = i;
              return;
            }
          });
          opt.walkType = !index ? 0 : index;
          portInfoList.push(opt);
        });
        savedata.portList = portInfoList;
        this.portInfoLists(savedata)
          .then(res => {
            if(res.status === 10000000) {
              setLoc('selport', this.$route.params.params);
              this.$router.go(-2);
              // clearLocalKey('netWorkNumlist');
            }
          });
      },
      back() {
        // setLoc('pageAdvScreenEnd', 10000001);
      },
      portInfoLists: async function(portInfoList) {
        const res = await htp.post(url.setAdvancedPointsScreen, portInfoList);
        return res;
      },
      //选择走线方式点击事件
      selectRouteOrder(index, val, allVal) {
        allVal.forEach(function(v, i) {
          v.checked = false;
          if(index === i) {
            v.checked = true;
          }
        });
      },
      loadamine() {
        if(!window.offline) {
          Indicator.open({
            text: 'Loading...',
            spinnerType: 'triple-bounce'
          });
        }
      },
      scroll() {
        if(document.activeElement.tagName === 'INPUT') {
          document.activeElement.blur();
        }
      }
    }
  };
</script>
<style lang="less" scoped>
  .vx-container {
    padding-top: 40px;
  }
  .screen_name,
  .screen_address {
    padding: 34px 35px;
    overflow: hidden;
    border-bottom: 1px solid #4f5054;
    display: flex;
    > div {
      width: 30%;
      text-align: left;
    }
    > div:nth-child(2) {
      text-align: left;
    }
  }
  .advancedScreen-list {
    color: #dddddd;
    background: #383940;
    font-size: 30px;
  }
  .advancedScreen-title {
    padding: 21px 35px;
    color: #aeaeae;
    text-align: left;
    font-size: 24px;
  }
  .routeOrder {
    padding: 18px 35px;
    border-bottom: 1px solid #4f5054;
    display: flex;
    // justify-content: space-between;
    > div:nth-child(1) {
      margin-top: 12px;
      width: 25%;
      text-align: left;
    }
    > div:nth-child(2) {
      width: 70%;
      height: 242px;
      display: flex;
      flex-wrap: wrap;
      padding-right: 12px;
      .imgParents {
        width: 25%;
        height: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        > div {
          width: 100px;
          height: 100px;
          border-radius: 8px;
        }
      }
    }
  }
  @media screen and (orientation: landscape) {
    .routeOrder {
      padding: 18px 35px;
      overflow: hidden;
      border-bottom: 1px solid #4f5054;
      display: flex;
      justify-content: space-between;
      height: 150px;
      > div:nth-child(1) {
        width: 20%;
        text-align: left;
        margin-top: 12px;
      }
      > div:nth-child(2) {
        width: 80%;
        height: 242px;
        .imgParents {
          float: left;
          margin-right: 14px;
          margin-bottom: 30px;
          width: 99px;
          height: 100px;
        }
      }
    }

    .screen_address,
    .screen_name {
      > div {
        width: 20%;
      }
    }
  }
  .normal_img1 {
    border: 1px solid #888b91; /*no*/
    background: url('../../assets/selectRouMethod/btn_1.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img1 {
    // background: url('../../assets/selectRouMethod/v1-pressed.png') center center / cover;
    background: #282828 url('../../assets/selectRouMethod/btn_1.png') center center no-repeat;
    border: 1px solid #e67007; /*no*/
    background-size: 65%;
  }
  .normal_img2 {
    border: 1px solid #888b91; /*no*/
    background: url('../../assets/selectRouMethod/btn_6.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img2 {
    background: #282828 url('../../assets/selectRouMethod/btn_6.png') center center no-repeat;
    border: 1px solid #e67007; /*no*/
    background-size: 65%;
  }
  .normal_img3 {
    border: 1px solid #888b91; /*no*/
    background: url('../../assets/selectRouMethod/btn_5.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img3 {
    background: #282828 url('../../assets/selectRouMethod/btn_5.png') center center no-repeat;
    border: 1px solid #e67007; /*no*/
    background-size: 65%;
  }
  .normal_img4 {
    border: 1px solid #888b91; /*no*/
    background: url('../../assets/selectRouMethod/btn_2.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img4 {
    background: #282828 url('../../assets/selectRouMethod/btn_2.png') center center no-repeat;
    border: 1px solid #e67007; /*no*/
    background-size: 65%;
  }
  .normal_img5 {
    border: 1px solid #888b91; /*no*/
    background: url('../../assets/selectRouMethod/btn_7.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img5 {
    background: #282828 url('../../assets/selectRouMethod/btn_7.png') center center no-repeat;
    border: 1px solid #e67007; /*no*/
    background-size: 65%;
  }
  .normal_img6 {
    border: 1px solid #888b91; /*no*/
    background: url('../../assets/selectRouMethod/btn_4.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img6 {
    background: #282828 url('../../assets/selectRouMethod/btn_4.png') center center no-repeat;
    border: 1px solid #e67007; /*no*/
    background-size: 65%;
  }
  .normal_img7 {
    border: 1px solid #888b91; /*no*/
    background: url('../../assets/selectRouMethod/btn_3.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img7 {
    background: #282828 url('../../assets/selectRouMethod/btn_3.png') center center no-repeat;
    border: 1px solid #e67007; /*no*/
    background-size: 65%;
  }
  .normal_img8 {
    border: 1px solid #888b91; /*no*/
    background: url('../../assets/selectRouMethod/btn_8.png') center center no-repeat;
    background-size: 65%;
  }
  .select_img8 {
    background: #282828 url('../../assets/selectRouMethod/btn_8.png') center center no-repeat;
    border: 1px solid #e67007; /*no*/
    background-size: 65%;
  }
</style>


